.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  /* gap: 5px;  */
  grid-template-areas:
    "a b c d"
    "f e g h"
    "i e j k"
    "l e m n"
    "o e p q"
    ;
}

